
/* video area begin*/
@media screen 
and (max-device-width: 900px) {
  .video-centering {
    display: block;
    margin: 0 auto;
    width: 256px;
    height: 144px;
  }
}

@media screen 
and (min-device-width: 911px) {
  .video-centering {
    display: block;
    margin: 0 auto;
    width: 560px;
    height: 315px;
  }
}
/* video area end*/


@media screen 
and (max-device-width: 900px) {
  .opis {
    margin: 3%;
    color: black;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 11px;
    animation-duration: 1s;
    animation-name: remarking;
  }
  @keyframes remarking {
      0% {
        color: rgb(245, 236, 236);
      }
      51% {
        transition: .3s;
        color: gold;
      }
      100% {
        transition: 0.3s;
        color: black
      }
  }
}
@media screen 
and (min-device-width: 911px) {
  .opis {
    margin: 3%;
    color: black;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 17px;
    animation-duration: 1s;
    animation-name: remarking;
  }
  @keyframes remarking {
      0% {
        color: rgb(245, 236, 236);
      }
      31% {
        transition: .3s;
        color: gold;
      }
      71% {
        transition: .3s;
        color: rgb(117, 5, 5);
        font-family: cursive;
      }
      100% {
        transition: 0.3s;
        color: blue;
      }
  }
}